'use client';

import { useState, useRef } from 'react';

export default function Home() {
  const [address, setAddress] = useState('');
  const [suggestions, setSuggestions] = useState([]);
  const [transactions, setTransactions] = useState([]);
  const [deletedTransactions, setDeletedTransactions] = useState([]);
  const [loading, setLoading] = useState(false);
  const [searchCenter, setSearchCenter] = useState(null);
  const [radius, setRadius] = useState(500); // Rayon par d√©faut 500m
  const [sortConfig, setSortConfig] = useState({ key: null, direction: 'asc' });
  const [filters, setFilters] = useState({
    Maison: true,
    Appartement: true,
    Local: false,
    Terrain: false
  });
  
  // Timer pour √©viter de spammer l'API adresse
  const debounceTimer = useRef(null);

  // Gestion de l'autocompl√©tion
  const handleAddressChange = (e) => {
    const val = e.target.value;
    setAddress(val);
    
    if (debounceTimer.current) clearTimeout(debounceTimer.current);

    if (val.length < 3) {
      setSuggestions([]);
      return;
    }

    debounceTimer.current = setTimeout(async () => {
      try {
        const res = await fetch(`https://api-adresse.data.gouv.fr/search/?q=${encodeURIComponent(val)}&limit=5`);
        const data = await res.json();
        setSuggestions(data.features || []);
      } catch (err) {
        console.error(err);
      }
    }, 300);
  };

  const selectAddress = (feature) => {
    setAddress(feature.properties.label);
    setSearchCenter({
      lon: feature.geometry.coordinates[0],
      lat: feature.geometry.coordinates[1]
    });
    setSuggestions([]);
  };

  // Formatage du cadastre
  const formatCadastre = (idParcelle) => {
    if (!idParcelle || typeof idParcelle !== 'string' || idParcelle.length !== 14) {
      return idParcelle || '-';
    }
    try {
      // Extraire la section (caract√®res 8 √† 10, index 8-9)
      const section = idParcelle.substring(8, 10);
      // Extraire le num√©ro (caract√®res 10 √† 14, index 10-13)
      const numeroRaw = idParcelle.substring(10, 14);
      // Enlever les z√©ros non significatifs
      const numero = parseInt(numeroRaw, 10).toString();
      return `${section} N¬∞${numero}`;
    } catch (error) {
      return idParcelle;
    }
  };

  // Calcul de distance (Haversine)
  const calculateDistance = (lat1, lon1, lat2, lon2) => {
    if (!lat1 || !lon1 || !lat2 || !lon2) return 0;
    const R = 6371e3;
    const œÜ1 = lat1 * Math.PI / 180;
    const œÜ2 = lat2 * Math.PI / 180;
    const ŒîœÜ = (lat2 - lat1) * Math.PI / 180;
    const ŒîŒª = (lon2 - lon1) * Math.PI / 180;
    const a = Math.sin(ŒîœÜ / 2) * Math.sin(ŒîœÜ / 2) +
              Math.cos(œÜ1) * Math.cos(œÜ2) *
              Math.sin(ŒîŒª / 2) * Math.sin(ŒîŒª / 2);
    const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));
    return Math.round(R * c);
  };

  // Lancer la recherche
  const searchDVF = async () => {
    if (!address) return alert("Adresse requise");
    setLoading(true);
    setSuggestions([]); // Cacher suggestions au cas o√π

    try {
      let center = searchCenter;
      
      // Si on n'a pas cliqu√© sur une suggestion, on g√©ocode l'adresse brute
      if (!center) {
        const geoRes = await fetch(`https://api-adresse.data.gouv.fr/search/?q=${encodeURIComponent(address)}&limit=1`);
        const geoData = await geoRes.json();
        if (!geoData.features?.length) throw new Error("Adresse introuvable");
        const coords = geoData.features[0].geometry.coordinates;
        center = { lon: coords[0], lat: coords[1] };
        setSearchCenter(center);
      }

      // Appel API interne avec le rayon
      const res = await fetch(`/api/search?lat=${center.lat}&lng=${center.lon}&radius=${radius}`);
      const data = await res.json();

      if (data.error) throw new Error(data.error);

      // Formatage des donn√©es
      const formatted = data.map(t => ({
        id: t.id_mutation || Math.random().toString(),
        date: new Date(t.date_mutation).toLocaleDateString('fr-FR', { day: '2-digit', month: '2-digit', year: 'numeric' }),
        dateRaw: new Date(t.date_mutation), // Pour le tri
        type: t.type_local,
        address: `${t.adresse_numero || ''} ${t.adresse_nom_voie || ''}, ${t.nom_commune || ''}`,
        cadastre: formatCadastre(t.id_parcelle),
        surface: t.surface_reelle_bati || 0,
        terrain: t.surface_terrain || 0,
        price: t.valeur_fonciere || 0,
        distance: calculateDistance(center.lat, center.lon, t.latitude, t.longitude)
      }));

      setTransactions(formatted);
      setDeletedTransactions([]); // R√©initialiser la corbeille lors d'une nouvelle recherche

    } catch (err) {
      alert("Erreur: " + err.message);
    } finally {
      setLoading(false);
    }
  };

  // Filtrage des donn√©es pour l'affichage (seulement les transactions actives)
  const filteredTransactions = transactions.filter(t => {
    if (!t.type) return false;
    const typeKey = Object.keys(filters).find(k => t.type.includes(k));
    return typeKey ? filters[typeKey] : false;
  });

  // Tri des transactions
  const sortedTransactions = [...filteredTransactions].sort((a, b) => {
    if (!sortConfig.key) return 0;
    
    let aValue, bValue;
    switch (sortConfig.key) {
      case 'date':
        aValue = a.dateRaw?.getTime() || 0;
        bValue = b.dateRaw?.getTime() || 0;
        break;
      case 'type':
        aValue = a.type || '';
        bValue = b.type || '';
        break;
      case 'address':
        aValue = a.address?.toLowerCase() || '';
        bValue = b.address?.toLowerCase() || '';
        break;
      case 'surface':
        aValue = a.surface || 0;
        bValue = b.surface || 0;
        break;
      case 'terrain':
        aValue = a.terrain || 0;
        bValue = b.terrain || 0;
        break;
      case 'price':
        aValue = a.price || 0;
        bValue = b.price || 0;
        break;
      case 'priceM2':
        aValue = a.surface > 0 ? (a.price / a.surface) : 0;
        bValue = b.surface > 0 ? (b.price / b.surface) : 0;
        break;
      case 'distance':
        aValue = a.distance || 0;
        bValue = b.distance || 0;
        break;
      default:
        return 0;
    }
    
    if (typeof aValue === 'string') {
      return sortConfig.direction === 'asc' 
        ? aValue.localeCompare(bValue)
        : bValue.localeCompare(aValue);
    }
    
    return sortConfig.direction === 'asc' 
      ? aValue - bValue
      : bValue - aValue;
  });

  // Gestion du tri
  const handleSort = (key) => {
    if (sortConfig.key === key) {
      // Inverser l'ordre si on clique sur la m√™me colonne
      setSortConfig({
        key,
        direction: sortConfig.direction === 'asc' ? 'desc' : 'asc'
      });
    } else {
      // Nouvelle colonne, tri ascendant par d√©faut
      setSortConfig({ key, direction: 'asc' });
    }
  };

  // Suppression (d√©placer vers la corbeille)
  const deleteTransaction = (id) => {
    const transaction = transactions.find(t => t.id === id);
    if (transaction) {
      setTransactions(prev => prev.filter(t => t.id !== id));
      setDeletedTransactions(prev => [...prev, transaction]);
    }
  };

  // Restauration (retirer de la corbeille)
  const restoreTransaction = (id) => {
    const transaction = deletedTransactions.find(t => t.id === id);
    if (transaction) {
      setDeletedTransactions(prev => prev.filter(t => t.id !== id));
      setTransactions(prev => [...prev, transaction]);
    }
  };

  // Calcul Stats
  const avgPriceM2 = filteredTransactions.reduce((acc, t) => acc + (t.surface ? t.price/t.surface : 0), 0) / (filteredTransactions.length || 1);
  const avgSurface = filteredTransactions.reduce((acc, t) => acc + parseInt(t.surface||0), 0) / (filteredTransactions.length || 1);

  return (
    <div className="bg-gray-50 min-h-screen text-gray-800 font-sans p-8">
      <div className="max-w-7xl mx-auto">
        <h1 className="text-4xl font-bold text-center text-slate-800 mb-8">
          Explorateur DVF <span className="text-blue-600 text-lg align-top">PRO (React)</span>
        </h1>

        {/* RECHERCHE */}
        <div className="bg-white p-6 rounded-xl shadow-lg mb-8 relative z-20">
          <div className="flex gap-4 relative">
            <div className="relative flex-1">
              <input 
                type="text" 
                value={address}
                onChange={handleAddressChange}
                placeholder="Entrez une adresse..." 
                className="w-full p-4 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 outline-none text-lg"
              />
              {suggestions.length > 0 && (
                <ul className="absolute left-0 right-0 bg-white border border-gray-200 rounded-b-lg shadow-xl mt-1 max-h-60 overflow-y-auto z-50">
                  {suggestions.map((s, i) => (
                    <li key={i} onClick={() => selectAddress(s)} className="px-4 py-2 hover:bg-gray-100 cursor-pointer border-b">
                      {s.properties.label}
                    </li>
                  ))}
                </ul>
              )}
            </div>
            <button onClick={searchDVF} className="bg-blue-600 hover:bg-blue-700 text-white px-8 rounded-lg font-bold">
              {loading ? '...' : 'Voir'}
            </button>
          </div>
          
          {/* S√©lection du rayon */}
          <div className="mt-4 flex items-center gap-4">
            <span className="text-sm font-semibold text-gray-700">Rayon de recherche :</span>
            <div className="flex gap-2">
              {[50, 100, 500, 1000].map(r => (
                <button
                  key={r}
                  onClick={() => setRadius(r)}
                  className={`px-4 py-2 rounded-lg font-medium transition-all ${
                    radius === r
                      ? 'bg-blue-600 text-white shadow-md'
                      : 'bg-gray-100 text-gray-700 hover:bg-gray-200'
                  }`}
                >
                  {r < 1000 ? `${r}m` : '1km'}
                </button>
              ))}
            </div>
          </div>
        </div>

        {/* DASHBOARD (visible si des donn√©es sont charg√©es) */}
        {transactions.length > 0 && (
          <div className="animate-fade-in-down">
            {/* STATS */}
            <div className="grid grid-cols-1 md:grid-cols-3 gap-4 mb-6">
              <div className="bg-white p-4 rounded-lg shadow border-l-4 border-blue-500">
                <p className="text-sm text-gray-500">Prix Moyen / m¬≤</p>
                <p className="text-2xl font-bold">{Math.round(avgPriceM2)} ‚Ç¨</p>
              </div>
              <div className="bg-white p-4 rounded-lg shadow border-l-4 border-green-500">
                <p className="text-sm text-gray-500">Biens affich√©s</p>
                <p className="text-2xl font-bold">{filteredTransactions.length}</p>
              </div>
              <div className="bg-white p-4 rounded-lg shadow border-l-4 border-purple-500">
                <p className="text-sm text-gray-500">Surface Moyenne</p>
                <p className="text-2xl font-bold">{Math.round(avgSurface)} m¬≤</p>
              </div>
            </div>

            {/* FILTRES */}
            <div className="bg-white p-4 rounded-lg shadow mb-6 flex gap-4 items-center flex-wrap">
              <span className="font-bold"><i className="fas fa-filter mr-2"></i>Filtres :</span>
              {Object.keys(filters).map(key => (
                <label key={key} className="inline-flex items-center cursor-pointer bg-gray-100 px-3 py-1 rounded-full hover:bg-gray-200">
                  <input 
                    type="checkbox" 
                    checked={filters[key]} 
                    onChange={e => setFilters({...filters, [key]: e.target.checked})}
                    className="form-checkbox h-5 w-5 text-blue-600"
                  />
                  <span className="ml-2">{key}</span>
                </label>
              ))}
            </div>

            {/* TABLEAU */}
            <div className="bg-white rounded-xl shadow-lg overflow-hidden mb-6">
              <table className="w-full text-left">
                <thead className="bg-gray-100 border-b">
                  <tr>
                    <th 
                      className="p-4 font-bold text-gray-500 text-xs uppercase cursor-pointer hover:bg-gray-200 transition-colors"
                      onClick={() => handleSort('date')}
                    >
                      Date {sortConfig.key === 'date' && (sortConfig.direction === 'asc' ? '‚Üë' : '‚Üì')}
                    </th>
                    <th 
                      className="p-4 font-bold text-gray-500 text-xs uppercase cursor-pointer hover:bg-gray-200 transition-colors"
                      onClick={() => handleSort('type')}
                    >
                      Type {sortConfig.key === 'type' && (sortConfig.direction === 'asc' ? '‚Üë' : '‚Üì')}
                    </th>
                    <th 
                      className="p-4 font-bold text-gray-500 text-xs uppercase cursor-pointer hover:bg-gray-200 transition-colors"
                      onClick={() => handleSort('address')}
                    >
                      Adresse {sortConfig.key === 'address' && (sortConfig.direction === 'asc' ? '‚Üë' : '‚Üì')}
                    </th>
                    <th className="p-4 font-bold text-gray-500 text-xs uppercase">Cadastre</th>
                    <th 
                      className="p-4 font-bold text-gray-500 text-xs uppercase text-right cursor-pointer hover:bg-gray-200 transition-colors"
                      onClick={() => handleSort('surface')}
                    >
                      Surf. Hab. {sortConfig.key === 'surface' && (sortConfig.direction === 'asc' ? '‚Üë' : '‚Üì')}
                    </th>
                    <th 
                      className="p-4 font-bold text-gray-500 text-xs uppercase text-right cursor-pointer hover:bg-gray-200 transition-colors"
                      onClick={() => handleSort('terrain')}
                    >
                      Surf. Ter. {sortConfig.key === 'terrain' && (sortConfig.direction === 'asc' ? '‚Üë' : '‚Üì')}
                    </th>
                    <th 
                      className="p-4 font-bold text-gray-500 text-xs uppercase text-right cursor-pointer hover:bg-gray-200 transition-colors"
                      onClick={() => handleSort('price')}
                    >
                      Prix {sortConfig.key === 'price' && (sortConfig.direction === 'asc' ? '‚Üë' : '‚Üì')}
                    </th>
                    <th 
                      className="p-4 font-bold text-gray-500 text-xs uppercase text-right cursor-pointer hover:bg-gray-200 transition-colors"
                      onClick={() => handleSort('priceM2')}
                    >
                      Prix/m¬≤ {sortConfig.key === 'priceM2' && (sortConfig.direction === 'asc' ? '‚Üë' : '‚Üì')}
                    </th>
                    <th 
                      className="p-4 font-bold text-gray-500 text-xs uppercase text-right cursor-pointer hover:bg-gray-200 transition-colors"
                      onClick={() => handleSort('distance')}
                    >
                      Dist. {sortConfig.key === 'distance' && (sortConfig.direction === 'asc' ? '‚Üë' : '‚Üì')}
                    </th>
                    <th className="p-4 text-center font-bold text-gray-500 text-xs uppercase">Action</th>
                  </tr>
                </thead>
                <tbody className="divide-y divide-gray-100">
                  {sortedTransactions.map(item => (
                    <tr key={item.id} className="hover:bg-gray-50">
                      <td className="p-4 text-sm text-gray-600">{item.date}</td>
                      <td className="p-4">
                        <span className={`px-2 py-1 rounded-full text-xs font-bold ${
                          item.type?.includes('Maison') ? 'bg-blue-100 text-blue-800' : 
                          item.type?.includes('Appartement') ? 'bg-yellow-100 text-yellow-800' :
                          item.type?.includes('Local') ? 'bg-green-100 text-green-800' : 
                          'bg-gray-100 text-gray-800'
                        }`}>
                          {item.type}
                        </span>
                      </td>
                      <td className="p-4 text-sm font-medium text-gray-900">{item.address}</td>
                      <td className="p-4 text-sm text-gray-600 font-mono">{item.cadastre}</td>
                      <td className="p-4 text-sm text-right text-gray-600">{item.surface} m¬≤</td>
                      <td className="p-4 text-sm text-right text-gray-600">{item.terrain > 0 ? `${item.terrain} m¬≤` : '-'}</td>
                      <td className="p-4 text-sm text-right font-bold text-gray-900">
                        {new Intl.NumberFormat('fr-FR', { style: 'currency', currency: 'EUR', maximumFractionDigits: 0 }).format(item.price)}
                      </td>
                      <td className="p-4 text-sm text-right text-blue-600 font-medium">
                        {item.surface > 0 ? `${Math.round(item.price / item.surface).toLocaleString('fr-FR')} ‚Ç¨/m¬≤` : '-'}
                      </td>
                      <td className="p-4 text-sm text-right text-gray-500">{item.distance} m</td>
                      <td className="p-4 text-center">
                        <button 
                          onClick={() => deleteTransaction(item.id)} 
                          className="bg-red-500 hover:bg-red-600 text-white px-3 py-1 rounded text-xs font-medium transition-colors"
                        >
                          Supprimer
                        </button>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
              {sortedTransactions.length === 0 && (
                <div className="p-8 text-center text-gray-500">Aucun r√©sultat avec ces filtres.</div>
              )}
            </div>

            {/* CORBEILLE / √âL√âMENTS IGNOR√âS */}
            {deletedTransactions.length > 0 && (
              <div className="bg-white rounded-xl shadow-lg overflow-hidden">
                <h2 className="p-4 bg-gray-100 border-b font-bold text-gray-700">üóëÔ∏è Corbeille / √âl√©ments ignor√©s</h2>
                <table className="w-full text-left">
                  <thead className="bg-gray-50 border-b">
                    <tr>
                      <th className="p-4 font-bold text-gray-500 text-xs uppercase">Date</th>
                      <th className="p-4 font-bold text-gray-500 text-xs uppercase">Type</th>
                      <th className="p-4 font-bold text-gray-500 text-xs uppercase">Adresse</th>
                      <th className="p-4 font-bold text-gray-500 text-xs uppercase">Cadastre</th>
                      <th className="p-4 font-bold text-gray-500 text-xs uppercase text-right">Surf. Hab.</th>
                      <th className="p-4 font-bold text-gray-500 text-xs uppercase text-right">Surf. Ter.</th>
                      <th className="p-4 font-bold text-gray-500 text-xs uppercase text-right">Prix</th>
                      <th className="p-4 font-bold text-gray-500 text-xs uppercase text-right">Prix/m¬≤</th>
                      <th className="p-4 font-bold text-gray-500 text-xs uppercase text-right">Dist.</th>
                      <th className="p-4 text-center font-bold text-gray-500 text-xs uppercase">Action</th>
                    </tr>
                  </thead>
                  <tbody className="divide-y divide-gray-100">
                    {deletedTransactions.map(item => (
                      <tr key={item.id} className="hover:bg-gray-50 opacity-75">
                        <td className="p-4 text-sm text-gray-600">{item.date}</td>
                        <td className="p-4">
                          <span className={`px-2 py-1 rounded-full text-xs font-bold ${
                            item.type?.includes('Maison') ? 'bg-blue-100 text-blue-800' : 
                            item.type?.includes('Appartement') ? 'bg-yellow-100 text-yellow-800' :
                            item.type?.includes('Local') ? 'bg-green-100 text-green-800' : 
                            'bg-gray-100 text-gray-800'
                          }`}>
                            {item.type}
                          </span>
                        </td>
                        <td className="p-4 text-sm font-medium text-gray-900">{item.address}</td>
                        <td className="p-4 text-sm text-gray-600 font-mono">{item.cadastre}</td>
                        <td className="p-4 text-sm text-right text-gray-600">{item.surface} m¬≤</td>
                        <td className="p-4 text-sm text-right text-gray-600">{item.terrain > 0 ? `${item.terrain} m¬≤` : '-'}</td>
                        <td className="p-4 text-sm text-right font-bold text-gray-900">
                          {new Intl.NumberFormat('fr-FR', { style: 'currency', currency: 'EUR', maximumFractionDigits: 0 }).format(item.price)}
                        </td>
                        <td className="p-4 text-sm text-right text-blue-600 font-medium">
                          {item.surface > 0 ? `${Math.round(item.price / item.surface).toLocaleString('fr-FR')} ‚Ç¨/m¬≤` : '-'}
                        </td>
                        <td className="p-4 text-sm text-right text-gray-500">{item.distance} m</td>
                        <td className="p-4 text-center">
                          <button 
                            onClick={() => restoreTransaction(item.id)} 
                            className="bg-green-500 hover:bg-green-600 text-white px-3 py-1 rounded text-xs font-medium transition-colors"
                          >
                            Restaurer
                          </button>
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>
            )}
          </div>
        )}
      </div>
    </div>
  );
}